<template lang="html">
  <div class="login-content">
    <div class="container">
      <div class="row">
        <div class="col-sm-6 col-md-4 col-md-offset-4 login-box">
          <h1 class="text-center login-title">Bienvenido</h1>
          <div class="account-wall">
            <img class="profile-img" src="https://lh5.googleusercontent.com/-b0-k99FZlyE/AAAAAAAAAAI/AAAAAAAAAAA/eu7opA4byxI/photo.jpg?sz=120" alt="">
              <form class="form-signin" v-on:submit.prevent="doLogin">
                <input type="email" v-model="user.email" class="form-control" placeholder="Email" required autofocus>
                <input type="password" v-model="user.password" class="form-control" placeholder="Contrasea" required>
                <button id="btn-login" class="btn btn-md btn-primary btn-block" type="submit">Iniciar Sesin</button>
                <label class="checkbox m-l-20">
                  <input type="checkbox" value="remember-me">Recurdame
                </label>
              </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      user: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapActions([
      'login'
    ]),
    doLogin () {
      window.$('#btn-login').button('loading')
      this.login(this.user)
      .then(() => {
        this.$router.replace(this.$route.query.redirect || '/admin')
        window.$('#btn-login').button('reset')
      })
      .catch(() => {
        window.$('#btn-login').button('reset')
      })
    }
  }
}
</script>

<style lang="scss">
  .login-content {
    height: 100vh;
    background-color: white;
    display: flex;
    align-items: center;
    .login-box{
      padding-bottom: 75px;
    }
    .m-l-20 {
      margin-left: 20px;
    }
    .form-signin {
        max-width: 330px;
        padding: 15px;
        margin: 0 auto;
    }
    .form-signin .form-signin-heading, .form-signin .checkbox {
        margin-bottom: 10px;
    }
    .form-signin .checkbox {
        font-weight: normal;
    }
    .form-signin .form-control {
        position: relative;
        font-size: 16px;
        height: auto;
        padding: 10px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .form-signin .form-control:focus {
        z-index: 2;
    }
    .form-signin input[type="text"] {
        margin-bottom: -1px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
    .form-signin input[type="password"] {
        margin-bottom: 10px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
    .account-wall {
        margin-top: 20px;
        padding: 40px 0px 20px 0px;
        background-color: #f7f7f7;
        -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
        -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    }
    .login-title {
        color: #555;
        font-size: 18px;
        font-weight: 400;
        display: block;
    }
    .profile-img {
        width: 96px;
        height: 96px;
        margin: 0 auto 10px;
        display: block;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        border-radius: 50%;
    }
    .need-help {
        margin-top: 10px;
    }
    .new-account {
        display: block;
        margin-top: 10px;
    }
  }
</style>



